import { Swiper, SwiperSlide } from 'swiper/react';

// IMAGE
import Unknown_person_img from "../../assets/images/Unknown_person.jpg";

// STYLE
import "swiper/css";
import "./style.css";

export default function PraySlider(props) {
  // PROPS
  const { data } = props;

  // RENDER
  return (
    <div className='pray-slider'>
      <div className='pray-slider__head'>
        <div className='pray-slider__title'>Cu cho cc linh hn trong ngy ny</div>
        <div className='pray-slider__desc'>c 1 kinh ly Cha + 1 kinh tin knh  ty lng mi ngi.</div>
      </div>
      <Swiper
        className='pray-slider__slider'
        slidesPerView={'auto'}
        spaceBetween={10}
        centeredSlides={true}
        initialSlide={1}
      >
        {data && data.map(item => (
          <SwiperSlide key={item.positionID}>
            <div className='pray-slider__item'>
              <div className='pray-slider__image'>
                <img src={item.img || Unknown_person_img} alt=''/>
              </div>
              <div className='pray-slider__info'>
                <div className='pray-slider__name'>{item.name || 'Cha c tn'}</div>
                <div className='pray-slider__year-of-dead'>Ngy mt: {item.yearOfDeadFormat || 'Cha thng tin'}</div>
                <div className='pray-slider__position-info'>
                  <div className='pray-slider__position-item'>S k: {item.shelf || 'Cha thng tin'}</div>
                  <div className='pray-slider__position-item'>S hng: {item.row || 'Cha thng tin'}</div>
                  <div className='pray-slider__position-item'>V tr: {item.number || 'Cha thng tin'}</div>
                </div>
              </div>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
      <div className='pray-slider__text-swipe'>(Vut sang tri  xem)</div>
    </div>
  );
}